import * as React from 'react';
import { css } from 'office-ui-fabric-react';

import styles from './MobilePreviewDeviceView.module.scss';
import {
  IDevice,
  DeviceOrientation,
  DeviceType
} from '../mobilePreview/MobilePreview';

export interface IMobilePreviewDeviceViewProps {
  currentDevice: IDevice;
  currentOrientation: DeviceOrientation;
  deviceType: DeviceType;
  url: string;
}

export default class MobilePreviewDeviceView extends React.Component<IMobilePreviewDeviceViewProps, void> {
  constructor(props: IMobilePreviewDeviceViewProps) {
    super(props);
  }

  public render(): React.ReactElement<IMobilePreviewDeviceViewProps> {
    const formStyle: React.CSSProperties = {
      width: this.props.currentDevice.width,
      height: this.props.currentDevice.height
    };

    const mobilePreviewClassName: string = css(
      styles.mobilePreviewDevice,
      'ms-borderColor-neutralSecondaryAlt',
      this.props.deviceType === DeviceType.Tablet ? styles.mobilePreviewTablet :
        this.props.currentOrientation === DeviceOrientation.Portrait ? styles.mobilePreviewPortrait :
        styles.mobilePreviewLandscape
    );

    const iFrameStyle: string = css(styles.mobilePreviewIframe, 'ms-bgColor-white');

    if (this.props.deviceType === DeviceType.Phone) {
      if (this.props.currentOrientation === DeviceOrientation.Landscape) {
        formStyle.width = this.props.currentDevice.height;
        formStyle.height = this.props.currentDevice.width;
      }
    }
    if (this.props.deviceType === DeviceType.Tablet) {
      if (this.props.currentOrientation === DeviceOrientation.Portrait) {
        formStyle.width = this.props.currentDevice.height;
        formStyle.height = this.props.currentDevice.width;
      }
    }

    return(
      <div className={ mobilePreviewClassName } style={ formStyle }>
        <iframe className={ iFrameStyle }
            src={ this.props.url } />
      </div>
    );
  }
}
